// variables
$mobile: new-breakpoint(min-width 480px);
$tablet: new-breakpoint(min-width 768px);
$desktop: new-breakpoint(min-width 994px);
$desktop-xl: new-breakpoint(min-width 1200px);

//350px
//$XS:    400px
$XS:    450px;
//500px
